:root {
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  background-color: var(--background);
  color: var(--text);
}

img {
  max-width: 100%;
}

.head-bar {
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--mode);
}

#switcher {
  color: var(--text);
  border: none;
  background: inherit;
}

h1 {
  font-size: 2rem;
}

/* .flag-card {
  max-width: 350px;
} */

#flag-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  font-size: 0.875rem;
}

#flag-wrapper .card {
  width: 250px;
}

img {
  max-width: 100%;
}

.flag-card {
  max-width: 350px;
}

.card {
  background-color: var(--mode);
}
